import styled from "styled-components";
import { AnimatePresence, motion } from "framer-motion";
import { useState } from "react";
import { useRecoilValue } from "recoil";
import { boxContents } from "./atom";

const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
  justify-content: center;
  align-items: center;
  background: linear-gradient(135deg, #00eea3, #d0e);
`;

const GridWrapper = styled.div`
  width: 50vw;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 10px;
  div {
    &:first-child {
      grid-column: span 2;
      transform-origin: bottom right !important;
      div {
        display: none;
      }
    }
    &:nth-child(2) {
      transform-origin: bottom left !important;
    }
    &:nth-child(3) {
      transform-origin: top right !important;
    }
    &:last-child {
      grid-column: span 2;
      transform-origin: top left !important;
      div {
        display: none;
      }
    }
  }
`;

const Overlay = styled(motion.div)`
  width: 100%;
  height: 100%;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  div {
    z-index: 100;
  }
`;

const Box = styled(motion.div)`
  display: flex;
  height: 200px;
  background-color: rgba(255, 255, 255, 0.7);
  border-radius: 20px;
  justify-content: center;
  align-items: center;
  p {
    padding: 15px;
    font-size: 13px;
    line-height: 15px;
    filter: blur(2px);
    cursor: pointer;
  }
`;

const OverlayText = styled(motion.p)`
  font-size: 17px !important;
  line-height: 18px !important;
`;

const Circle = styled(motion.div)`
  z-index: 10;
  width: 50px;
  height: 50px;
  border-radius: 25px;
  background-color: rgb(255, 255, 255);
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
`;

const Switchbtn = styled(motion.button)`
  margin: 35px 0;
  border: none;
  padding: 10px 15px;
  border-radius: 5px;
  font-size: 14px;
  cursor: pointer;
`;

const layoutVariants = {
  invisible: { backgroundColor: "rgba(50, 50, 50, 0)" },
  visible: { backgroundColor: "rgba(50, 50, 50, 0.7)" },
  out: { backgroundColor: "rgba(50, 50, 50, 0)" },
};

const OverlayTextVar = {
  blur: { filter: "blur(2px)" },
  unblur: { filter: "blur(0)", transition: { duration: 0.1 } },
  bluragain: { filter: "blur(2px)", transition: { duration: 0.5 } },
};

const SwitchbtnVar = {
  hover: {
    scale: 1.3,
    color: "rgb(0, 195, 255)",
    transition: { duration: 0.1 },
  },
  tapping: { color: "rgb(255, 183, 0)", transition: { duration: 0.3 } },
};

function Animation() {
  const contents = useRecoilValue(boxContents);
  const [id, setId] = useState<null | number>(null);
  const [switching, setSwitching] = useState(false);
  const toggleSwitch = () => {
    setSwitching((prev) => !prev);
  };
  return (
    <Wrapper>
      <GridWrapper>
        {contents.map((n) => (
          <Box
            whileHover={{ scale: 1.1 }}
            key={n.id}
            onClick={() => setId(n.id)}
            layoutId={n.id + ""}
          >
            {n.id === 2 ? (
              !switching ? (
                <Circle layoutId="set" style={{ borderRadius: 25 }} />
              ) : null
            ) : n.id === 3 ? (
              switching ? (
                <Circle layoutId="set" style={{ borderRadius: 10 }} />
              ) : null
            ) : (
              <p>{n.text}</p>
            )}
          </Box>
        ))}
      </GridWrapper>
      <Switchbtn
        variants={SwitchbtnVar}
        whileHover="hover"
        whileTap="tapping"
        onClick={toggleSwitch}
      >
        Switch
      </Switchbtn>
      <AnimatePresence>
        {id ? (
          <Overlay
            onClick={() => setId(null)}
            variants={layoutVariants}
            initial="invisible"
            animate="visible"
            exit="out"
          >
            <Box
              layoutId={id + ""}
              style={{
                width: 500,
                height: 250,
                backgroundColor: "rgba(255,255,255,1)",
              }}
            >
              {contents.map((n) =>
                n.id === id ? (
                  <OverlayText
                    variants={OverlayTextVar}
                    initial="blur"
                    animate="unblur"
                    exit="bluragain"
                    key={n.id}
                  >
                    {n.text}
                  </OverlayText>
                ) : null
              )}
            </Box>
          </Overlay>
        ) : null}
      </AnimatePresence>
    </Wrapper>
  );
}

export default Animation;
